export default class MessageService {
  _toast = null;
  constructor(toast) {
    this._toast = toast;
  }

  show(title, message) {
    this._toast.toast(message, {
      title: title,
      autoHideDelay: 5000,
      appendToast: true,
    });
  }

  showError(errorMessage) {
    this._toast.toast(errorMessage, {
      title: " ",
      autoHideDelay: 5000,
      appendToast: true,
      variant: "danger",
    });
  }

  showDelete(message) {
    this.show("", "  " + message + " ");
  }

  showCreate(message) {
    this.show("", "  " + message + " ");
  }

  showUpdate(message) {
    this.show("", "  " + message + " ");
  }
}